import React, { useState, useEffect } from 'react';
import { initMiniApp , initMainButton  } from '@telegram-apps/sdk';

const SDKReact: React.FC = () => {

    const [mainButton] = initMainButton();
    const [miniApp] = initMiniApp();

    useEffect(() => {
        miniApp.ready();
    }, []);

    const onShow = () => {
        mainButton.show();
        console.log(mainButton.isVisible);
    }

    return (
        <div className="w-full text-white h-full font-bold flex flex-col max-w-xl">
            <div className="relative min-h-screen loading-screen pb-7">
                <h1>SDK React</h1>
                <button className='text-xl mb-2' onClick={onShow}>Test Main Button</button>
            </div>
        </div>
    );
};

export default SDKReact;